
.Info{
  position: absolute;
  top: 0;
  display: grid;
  height: 100vh;
  width: 100vw;
  overflow-y: auto;

  box-sizing: border-box;
  background-color: #F7F1E5;
  color: #000000;
  font-family: var(--ko-font);
}

/* 화면에서 스크롤바 안보이게 */
.Info::-webkit-scrollbar {
  display: none;
}

.inner {
  height: 100vh;
  display: flex;
}

.top{
  box-sizing: border-box;
  padding: 300px 50px 50px 50px;

  display: grid;
  grid-template-columns: 1fr 1.2fr;
}

.desc{
  display: grid;
  align-content: space-between;
}

.skill {
  display: grid;
  grid-template-columns: 1.5fr 1fr;
}

.skill-child{
  display: grid;
  align-content: space-between;
}

.goal {
  color: #035FC4 ! important;
  -webkit-text-stroke: 1px #035FC4 ;
  font-weight: 900;
  font-size: 2.7rem;
}

.contact{
  font-size: 1.3rem;
  font-weight: 600;
}
.stress{
  font-weight: 600;
  padding-bottom: 0;
}

.self-wrapper{
  padding-top: 20px;
  font-size: 1.2rem;
  font-weight: bold;
  color: #646464;

  display: grid;
}

.heading{
  font-size: 1.6rem;
  font-weight: 700;
  color: rgb(23, 23, 23);
  padding-bottom: 10px;
}

.sub{
  font-size: 1rem;
}
/* 2page */
.bottom{
  display: grid;
  box-sizing: border-box;
  padding: 150px 50px 0px 50px;
}

.bottom > h3{
    padding-bottom: 5px;
    box-sizing: border-box;
}

.bottom > .wrapper{
  display: flex;
  justify-content: space-between;
  color: #000000 !important;
  font-weight: bolder;
  padding-bottom: 20px;
  box-sizing: border-box;
}

.bottom > .wrapper > div > h4{
  margin: 0;
}

.bottom > .wrapper > div > h5{
  margin: 0;
  color: #646464 !important;
}

.timeline{
  border-left: 2px solid #b3b1b1;
  padding-left: 20px;
  box-sizing: border-box;
}

.works-wrapper{
  height: 430px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding-top: 20px;
  box-sizing: border-box;
  gap: 0px 10px;
}

.works{
  display: grid;
  gap: 0px;
  padding: 3px 0;
  box-sizing: border-box;
}

.works > p {
    margin: 0;
    font-size:0.7rem;
}

@media all and (min-width:768px) and (max-width:1200px){
	.top{
    height: 90vh;
    padding: 200px 30px 30px 30px;
    box-sizing: border-box;
  
    display: grid;
    grid-template-columns: 1fr 1.1fr;
  }
  .skill {
    display: grid;
    grid-template-columns: 1.2fr 1fr;
  }
  .goal {
    font-size: 1.9rem;
  }
  .contact{
    font-size: 1.1rem;
  }
  .bottom{
    padding: 170px 30px 30px 30px;
  }
  .bottom > h3{
    font-size: 1.3rem;
}
  .bottom > .wrapper{
    display: flex;
    justify-content: space-between;
    width: 100%;
    box-sizing: border-box;
  }
  
  .works-wrapper{
    height: 440px;
  }

  .wrapper >div:nth-of-type(1){
    width: 45%;
  }
  .wrapper >div:nth-of-type(2){
    width: 28%;
  }
  .wrapper >div:nth-of-type(3){
    width: 27%;
  }
  .works{
    padding: 5px 0;
    box-sizing: border-box;
  }

  .works > p {
    font-size: 0.6rem;
  }
  .timeline{
    border-left: 1px solid #b3b1b1;
  }
}

/*모바일 가로, 세로 : -767px*/
@media all and (max-width:767px){
  .top{
    padding: 120px 20px 20px 20px;
  }

  .skill {
    display: grid;
    grid-template-columns: 1.5fr 1fr;
  }
  
  .goal {
    font-size: 1.2rem;
  }
  
  .contact{
    font-size: 0.8rem;
  }

  .heading{
    font-size: 1rem;
    padding-bottom: 5px;
  }

  .self-wrapper{
    padding-top: 10px;
    font-size: 0.7rem;
  }
  
  .sub{
    font-size: 0.7rem;
  }

  /* 2page */
  .bottom{
    padding: 100px 20px 20px 20px;
  }

  .wrapper{
    display: grid !important;
  }

  .timeline{
    border-left: none;
    padding-left: 0px;
  }

}