.Experience{
  display: grid;
  font-family: var(--default-font);
}

.Experience-wrapper{
  display: grid;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 5% 0;
  box-sizing: border-box;
}

.heading{
  margin-bottom: 40px;
  font-size: 3rem;
}

.buttons{
  display: flex;
  gap: 15px;
  font-size: 1.5rem;
}
.buttons-item {
  background-color: #646464;
  color: #e7e7e7;
  border-radius: 15px;
  padding: 5px 10px;
  box-sizing: border-box;
  font-size: 1rem;
}

.buttons-item_active{
  background-color: #035FC4;
  color: #ffffff;
  border-radius: 15px;
  padding: 5px 10px;
  box-sizing: border-box;
  font-size: 1rem;
}

.buttons-item:hover{
  cursor: pointer;
}

@media all and (min-width:768px) and (max-width:1200px){
	.Experience-wrapper{
    padding: 8% 0;
  }
}

/*모바일 가로, 세로 : -767px*/
@media all and (max-width:767px){
  .heading{
    margin: 100px 0 40px 0;
    font-size: 2rem;
  }
}