.header{
  width: 100%;
  display: flex;
  justify-content: space-between;
  text-align: start;

  padding: 40px;
  box-sizing: border-box;
  
  font-family: var(--default-font);
  font-size: 1.7rem;
  font-weight: 500;
  color: #101010;

  position: fixed;
  z-index: 1;
}

.colored{
  background: #ffffff;
}

.header > div > img {
  width: 25%;
  z-index: 2;
}

.header > div:hover{
  cursor: pointer;
}

.header > ul {
  list-style: none;
  display: block;
  margin: 0;
  text-align: end;
  z-index: 2;
}

.header > ul > li{
    cursor: pointer;
    padding-bottom: 5px;
}

.active{
  color: #646464 ! important;
}

@media all and (min-width:768px) and (max-width:1200px){
  .header{
    padding: 20px;
    box-sizing: border-box;
    font-size: 1.4rem;
    font-weight: 300;
  }

  .header > div > img {
    width: 20%;
  }
}

/*모바일 가로, 세로 : -767px*/
@media all and (max-width:767px){
  .header{
    padding: 15px;
    box-sizing: border-box;
    font-size: 1.2rem;
    font-weight: 300;
    /* z-index: 1; */
  }

  .header > div > img {
    width: 15%;
  }
}

@media all and (max-width:450px){
  .header > div > img {
    width: 25%;
  }
}