.Detail{
  display: grid;
  box-sizing: border-box;
  font-family: var(--ko-font);
  padding: 60px 40px;
  padding-top: 200px;
}

.Detail > div {
  padding: 10px;
  box-sizing: border-box;
}

.Detail > div > img {
  width: 100%;
}

.desc{
  display: flex;
  justify-content: space-between;
  padding: 0 30px ! important;
  box-sizing: border-box;
}

.title_en {
  font-weight: 900;
  font-size: 3rem;
  /* font-size: 2.3rem; */
  text-transform: uppercase;
  margin: 0px;
}

.desc > div > h3 {
  margin-top: 7px;
  color: #646464;
}

.etc{
  text-align: end;
  font-weight: 700;
  padding-bottom: 80px;
  margin-top: 1em;
}

.etc > p {
  margin: 5px;
}

.icons{
  text-align: right;
  display: flex;
  flex-direction: row-reverse;
  /* gap: 10px; */
}

.icons > div > img{
  height: 80px;
}

.images{
  display: grid;
  align-items: center;
  gap: 20px;
  
}
.images > div {
  display: flex;
  justify-content: space-between;
}

.images > div > img {
  width: 100%;
}