
.box {
  width: 100%;
  padding: 0 50px;
  box-sizing: border-box;
  text-align: center;
}

.cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  padding: 0;
  list-style-type: none;
}

.card {
  display: block;
  cursor: pointer;
}

.card_active {
  display: block;
  cursor: pointer;

  position: relative;
  animation: fadeInUp linear;
}

.image__wrapper {
  width: 100%;
}

.card > div {
  display: grid;
}

.card__image {
  border-radius: 10px;
  object-fit: cover;
  width: 100%;
  height: 310px;
}

.card_title {
  font-family: var(--ko-font);
  font-size: 1rem;
  margin: -2px 0 0.3em;
  color: #a09f9f;
  text-align: center;
  align-items: start;
  font-weight: bold;
  padding-top: 5px;
}

.card_title > p {
  margin: 0;
  font-size: 1.2rem;
}
.card_title > p:nth-child(1){
  color: black !important;
}
.size_down{
  font-size: 1rem !important;
}
@keyframes fadeInUp {
  0% {
      opacity: 0;
      transform: translate3d(0, 100%, 0);
  }
  to {
      opacity: 1;
      transform: translateZ(0);
  }
}

@media all and (min-width:768px) and (max-width:1200px){
  .box {
    padding: 0 30px;
  }
  .card__image {
    height: 230px;
  }

  .card_title > p {
    font-size: 1rem;
  }

  .size_down{
    font-size: 0.8rem !important;
  }
}

/*모바일 가로, 세로 : -767px*/
@media all and (max-width:767px){
  .box {
    padding: 0 10px;
  }
  
  .cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
  }

  .card__image {
    border-radius: 10px;
    height: 150px;
  }

  .card_title > p {
    font-size: 0.6rem;
  }

  .size_down{
    font-size: 0.5rem !important;
  }
}
