/* 수정 예정 */
.swipe-item {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%; 
    background-size: cover;
  
    animation-name: swipe;
    animation-duration: 40s;
    animation-iteration-count: infinite;
    animation-fill-mode: forwards;
  }

  @keyframes swipe {
    0%{
      background: url("../../asset/swipe/001.jpg");
      /* background-repeat: no-repeat; */
      background-size: cover;
    }
    20%{
      background: url("../../asset/swipe/002.JPG");
      /* background-repeat: no-repeat; */
      background-size: cover;
    }
    60%{
      background: url("../../asset/swipe/003.jpg");
      /* background-repeat: no-repeat; */
      background-size: cover;
    }
    80%{
      background: url("../../asset/swipe/004.jpg");
      /* background-repeat: no-repeat; */
      background-size: cover;
    }
    100%{
      background: url("../../asset/swipe/005.jpg");
      /* background-repeat: no-repeat; */
      background-size: cover;
    }
  }

  @keyframes fade-in {
    from {
      opacity: 0.5;
    }
    to {
      opacity: 1;
    }
  }
  
  @keyframes fade-out {
    from {
      opacity: 1;
    }
    to {
      opacity: 0.5;
    }
  } 
